import Grid from '@toast-ui/react-grid';
import React, { useEffect, useState } from 'react';
import 'tui-grid/dist/tui-grid.css';
import 'tui-date-picker/dist/tui-date-picker.css';

class CheckboxRenderer {
  constructor(props) {
    const { grid, rowKey } = props;

    const label = document.createElement('label');
    label.className = 'checkbox tui-grid-row-header-checkbox';
    label.setAttribute('for', String(rowKey));

    const hiddenInput = document.createElement('input');
    hiddenInput.className = 'hidden-input';
    hiddenInput.id = String(rowKey);

    const customInput = document.createElement('span');
    customInput.className = 'custom-input';

    label.appendChild(hiddenInput);
    label.appendChild(customInput);

    hiddenInput.type = 'checkbox';
    label.addEventListener('click', (ev) => {
      ev.preventDefault();

      if (ev.shiftKey) {
        grid[!hiddenInput.checked ? 'checkBetween' : 'uncheckBetween'](rowKey);
        return;
      }

      grid[!hiddenInput.checked ? 'check' : 'uncheck'](rowKey);
    });

    this.el = label;
    this.render(props);
  }

  getElement() {
    return this.el;
  }

  render(props) {
    const hiddenInput = this.el.querySelector('.hidden-input');
    const checked = Boolean(props.value);

    hiddenInput.checked = checked;
  }
}

//const [columns, setColumns] = useState([]);

const TuiGrid = (props) => {
  const { list } = props;

  // useEffect(() => {
  //   fetch('http://localhost:8080/api/SRM_100R/headerlist')
  //     .then((res) => res.json())
  //     .then((res) => {
  //       setColumns(res);
  //     });
  // }, []);

  const columns = [
    {
      header: '',
      name: 'PUR_ORDER_DT',
      editor: {
        type: 'datePicker',
        options: {
          format: 'yyyy/MM/dd',
        },
      },
    },
    { name: 'PUR_CUST_CD', header: '', editor: 'text', visible: false },
    { name: 'BP_NM', header: '', editor: 'text' },
    { name: 'PUR_ORDER_NO', header: '', editor: 'text' },
    { name: 'PUR_ORDER_SEQ', header: '', editor: 'text' },
    { name: 'PUR_USER_ID', header: '' },
    {
      name: 'PLANT_TYPE',
      header: '',
      formatter: 'listItemText',
      editor: {
        type: 'select',
        options: {
          listItems: [
            { text: '1', value: '1' },
            { text: '2', value: '2' },
            { text: '3', value: '3' },
          ],
        },
      },
    },
    { name: 'PUR_STATUS', header: '' },
    {
      type: 'checkbox',
      header: (
        <label for="all-checkbox" class="checkbox">
          <input type="checkbox" id="all-checkbox" class="hidden-input" name="_checked"></input>
          <span className="custom-input"></span>
        </label>
      ),
      name: 'SUPPLY_TYPE',
      renderer: {
        type: CheckboxRenderer,
      },
    },
    { name: 'ITEM_CD', header: '', editor: 'text' },
    { name: 'ITEM_NM', header: '', editor: 'text' },
    { name: 'SPEC', header: 'SPEC', editor: 'text' },
    { name: 'THICK', header: '', editor: 'text' },
    { name: 'WIDTH', header: '', editor: 'text' },
  ];

  const [data, setData] = useState([
    {
      PUR_ORDER_DT: '2022-04-01',
      PUR_CUST_DT: 'S11',
      BP_NM: '',
      PUR_ORDER_NO: 123,
      PUR_USER_ID: '',
      PLANT_TYPE: '',
      PUR_STATUS: '',
      SUPPLY_TYPE: 'Y',
      ITEM_CD: '',
      ITEM_NM: '',
      SPEC: 'SPEC',
      THICK: '',
      WIDTH: '',
    },
  ]);

  //const [data, setData] = useState([]);
  //setData(list);

  return (
    <Grid
      className="grid"
      data={list}
      columns={columns}
      rowHeight={20}
      bodyHeight={500}
      heightResizable={true}
      rowHeaders={['rowNum']}
      columnOptions={{ resizable: true }}
    />
  );
};

export default TuiGrid;
