import React, { useEffect, useState } from 'react';
import TuiGrid from '../components/TuiGrid';

const SRM_100R = () => {
  const [list, setList] = useState([]);

  useEffect(() => {
    fetch('http://localhost:8080/api/SRM_100R/list')
      .then((res) => res.json())
      .then((res) => {
        console.log(res);
        setList(res);
      }); //  
  }, []);

  return (
    <div>
      <h1></h1>
      <TuiGrid list={list} />
    </div>
  );
};

export default SRM_100R;
